.login-btn {
  margin-right: 1rem;
}

.yellow-input {
  border-bottom: 1px solid #fff !important;
  box-shadow: 0 1px 0 0#fff !important;
}

.yellow-input + label {
  color: #fff !important;
}

.yellow-input + label:focus {
  color: #ffeb3b !important;
}

.yellow-input:focus {
  border-bottom: 1px solid #ffeb3b !important;
  box-shadow: 0 1px 0 0#ffeb3b !important;
}
